@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap');

*{
  margin: 0;
}
.navbar{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  display: flex;
  flex-direction: row;
  padding: 1rem 0;
  justify-content: space-between;
}
.image{
  width: 18%;
  margin: auto 1rem;
}
.troll-image{
  width: 100%;
}
.info{
  display: flex;
  flex-direction: row;
  margin-left: 3rem;
}
.nav-title{
  color: #fff;
  margin: auto 0.4rem;
  font-size: clamp(1.2em, 2.5vw, 2.5rem);
}
.nav-info{
  color: #ffff;
  margin: auto 3rem;
  font-size: clamp(0.8em, 2vw, 1.5rem);
}

/*memes form style */
.meme-form{
display: flex;
flex-direction: column;
width: 70%;
margin: 2rem auto;
}
.input-box{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}
.meme-input,.sign-up-input{
  border: 1.5px solid rgb(159, 159, 159);
  padding: 1rem 0.5rem;
  width: 40%;
  margin: 1rem 0;
  color: #aaa;
  font-size: clamp(0.7em, 1.8vw, 1rem);
  border-radius: 0.3rem;
}
.meme-btn{
  width: 100%;
  margin: 1rem auto;
  padding: 0.7rem 1rem;
  font-size: clamp(0.5em, 1.4vw, 1.5rem);
  border-radius: 0.3rem;
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  color: white;
  border: none;
  box-shadow: 0 3px 15px -4px #aaa;
  cursor: pointer;
}
.meme-image-container{
  width: 90%;
  margin: 2rem auto;
  position: relative;
}
.meme-image{
  width: 100%;
  border:  1px solid transparent;
  border-radius: 0.6rem;
}
.meme-top-text{
  position: absolute;
  top: 50px;
  left: 100px;
  color: white;
  font-family: 'Bebas Neue', cursive;
  -webkit-text-stroke-color: black; 
  -webkit-text-stroke-width: 0.1px;
  font-size: clamp(1em, 6vw, 6rem);
}
.meme-bottom-text{
  position: absolute;
  bottom: 50px;
  left: 100px;
  font-family: 'Bebas Neue', cursive;
  color: white;
  -webkit-text-stroke-color: black; 
  -webkit-text-stroke-width: 0.1px;
  font-size: clamp(1em, 6vw, 6rem);
}

.download-button{
  background: linear-gradient(90deg, #672280 1.18%, #A626D3 100%);
  padding: 0.4rem 1rem;
  color: white;
  text-decoration: none;
  border-radius: 0.3rem;
  margin: 1rem auto;
  font-size: clamp(0.5em, 1.4vw, 1.5rem);
}

@media(max-width:500px){
  .meme-form{
    width: 90%;
  }
  .input-box{
    flex-direction: column;
  }
  .meme-input{
    width: 90%;
  }
  .meme-btn{
    width: 50%;
  }
  /*header section style */
  .info{
    margin-left: 0.4rem;
  }
  .image{
    margin: auto 0.3rem;
  }
  .nav-info{
    margin: auto 0.4rem;
  }
}